export class Grid {
  constructor(args) {
    let def = {
      x: 0,
      y: 0
    }
    Object.assign(def, args)
    Object.assign(this, def)
  }
  draw(ctx) {
    ctx.save()
    let span = 50
    ctx.beginPath()
    for (let i = 0; i < this.x; i += span) {
      ctx.moveTo(i, 0)
      ctx.lineTo(i, this.y)
    }
    for (let j = 0; j < this.y; j += 50) {
      ctx.moveTo(0, j)
      ctx.lineTo(this.x, j)
    }
    ctx.strokeStyle = 'rgba(255, 255, 255, 0.2)'
    ctx.stroke()
    ctx.restore()
  }
}



