<script>
import throttle from 'lodash.throttle'
import { Vec2 } from '@/classes/vector'
import { Grid } from '@/classes/Grid'
import { Weapon } from '@/classes/Weapon'
import { Bullet } from '@/classes/Bullet'
// import { CBullet } from '@/classes/CBullet'
import { CircleEnemy } from '@/classes/CircleEnemy'
import { Life } from '@/classes/Life'
import { Timer } from '@/classes/Timer'

export default {
  name: 'startPage',
  props: {
    color: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      colorBG: '#001D2E',
      colorYellow: '#F5AF5F',
      colorYellowLight: 'rgb(245, 175, 95, 2)',
      colorBlue: '#3676BB',
      colorRed: '#E7465D',
      canvas: null,
      context: null,
      grid: null,
      mouse: new Vec2(0, 0),
      isStart: false,
      weapon: null,
      degToPi: Math.PI * 2 / 360,
      bullets: [],
      time: 0,
      angle: 0,
      cEnemy: [],
      cBullets: [],
      game: null,
      isOver: false,
      life: null,
      lifeChecker: null,
      throttleFunc: null,
      deadline: null,
      timer: null
    }
  },
  mounted() {
    this.initCanvas()
    this.init()
    // this.drawGrid()
    // this.drawStartPage()
    // this.drawGame()
    // requestAnimationFrame(this.drawGame)
    let fps = 60
    this.game = setInterval(this.update, 1000 / fps)
    this.lifeChecker = setInterval(this.lifeDetect, 1000)
    requestAnimationFrame(this.draw)
  },
  computed: {
    ww() {
      return window.innerWidth
    },
    wh() {
      return window.innerHeight
    },
    wGridNum() {
      return Math.ceil(this.canvas.width / 50)
    },
    wCenter() {
      return this.canvas.width / 2
    },
    hCenter() {
      return this.canvas.height / 2
    }
  },
  methods: {
    initCanvas() {
      this.canvas = document.getElementById('mycanvas')
      this.canvas.width = this.ww
      this.canvas.height = this.wh
      this.context = this.canvas.getContext('2d')
    },
    init() {
      this.grid = new Grid({
        x: this.ww,
        y: this.wh
      })
      this.life = new Life({
        x: this.ww - 50,
        y: 50,
        w: 400,
        h: 30,
        value: 100,
        heart: 3
      })
      this.weapon = new Weapon({
        x: this.wCenter,
        y: this.hCenter,
        deg: 0 * this.degToPi,
        r1: 60,
        r2: 100
      })
      let c = new CircleEnemy({
        x: this.wCenter,
        y: this.hCenter,
        r: 30,
        deg: 0 * this.degToPi,
        span: this.weapon.r4,
        color: this.colorYellow,
        color2: this.colorYellowLight
      })
      this.cEnemy.push(c)
      this.throttleFunc = throttle(this.newShot, 60)

      this.timer = new Timer({
        x: this.wCenter,
        y: 50
      })

      document.addEventListener(
        'keydown',
        event => {
          if (this.life.heart > 0) {
            // console.log(event.keyCode)
            let keycode = event.keyCode
            if (keycode === 37) {
              this.subDegree()
            } else if (keycode === 39) {
              this.addDegree()
            } else if (keycode === 87) {
              // this.newShot()
              this.throttleFunc()
            }
          }
        },
        false
      )
      document.addEventListener('keyup', event => {})
    },
    keyEvtHandler() {},
    subDegree() {
      this.angle -= Math.PI / 180 * 15
    },
    addDegree() {
      this.angle += Math.PI / 180 * 15
    },
    update() {
      this.timer.update()
      this.weapon.deg = this.angle

      this.bullets.forEach(b => b.update())

      this.cEnemy.forEach(c => {
        c.update()
        c.bullets.forEach((cb, idx, array) => {
          cb.update()
        })
      })
    },
    draw() {
      if (this.isOver) {
        clearInterval(this.game)
      } else {
        // this.context.clearRect(0, 0, this.ww, this.wh)
        this.context.fillStyle = this.colorBG
        this.context.fillRect(0, 0, this.ww, this.wh)

        if (this.time > 10000) this.time = 0
        this.time++
        if (this.time % 100 === 0) {
          this.cEnemy.forEach(c => {
            c.addBullet()
            c.clockwise = Math.random() > 0.5
          })
        }
        if (this.time % 600 === 0) {
          this.newCEnemy()
        }

        // 格線
        this.grid.draw(this.context)
        // 倒數計時器
        this.timer.draw(this.context)
        // 生命條
        this.life.draw(this.context)
        // 武器
        this.weapon.draw(this.context)

        // 子彈
        this.bullets.forEach((b, bidx, barr) => {
          if (b.x < 0 || b.x > this.ww || b.y < 0 || b.y > this.wh) {
            barr.splice(bidx, 1)
          }
          // console.log(`(${b.x}, ${b.y})`)
          this.cEnemy.forEach((c, cidx, carr) => {
            let cVec = new Vec2(
              c.x + c.span * Math.cos(c.deg),
              c.y + c.span * Math.sin(c.deg)
            )
            let distance = Math.sqrt(
              Math.pow(b.x - cVec.x, 2) + Math.pow(b.y - cVec.y, 2)
            )
            // console.log(`(${cVec.x}, ${cVec.y})`)
            // console.log(distance)
            if (distance < 5 + c.r) {
              barr.splice(bidx, 1)
              carr.splice(cidx, 1)
            }
          })
          b.draw(this.context)
        })

        this.cEnemy.forEach(c => {
          c.draw(this.context)
          c.bullets.forEach((cb, idx, cbarr) => {
            cb.draw(this.context)
            // 抓單位向量
            let v = new Vec2(cb.x - this.wCenter, cb.y - this.hCenter)

            if (v.length - 10 <= this.weapon.r3) {
              let defenseSuccess =
                cb.deg >= this.angle + 125 * this.degToPi &&
                cb.deg <= this.angle + 235 * this.degToPi
              if (defenseSuccess) {
                cbarr.splice(idx, 1)
                // this.isOver = true
              } else {
                // 要先扣除子彈彈頭的半徑
                if (v.length - 10 <= this.weapon.r2) {
                  // this.isOver = true
                  this.life.update()
                  cbarr.splice(idx, 1)
                }
              }
            }
          })
        })

        if (this.cEnemy.length === 0) {
          this.newCEnemy()
        }
      }
      requestAnimationFrame(this.draw)
      if (this.timer.timesup) {
        this.isOver = true
      }
    },
    newShot() {
      let b = new Bullet({
        x: this.wCenter + Math.cos(this.weapon.deg) * this.weapon.r2 - 5,
        y: this.hCenter + Math.sin(this.weapon.deg) * this.weapon.r2 - 5,
        v: {
          x: Math.cos(this.weapon.deg) * 4,
          y: Math.sin(this.weapon.deg) * 4
        }
      })
      this.bullets.push(b)
    },
    newCEnemy() {
      let c = new CircleEnemy({
        x: this.wCenter,
        y: this.hCenter,
        r: 30,
        deg: Math.ceil(Math.random() * 360) * this.degToPi,
        span: this.weapon.r4,
        color: this.colorYellow,
        color2: this.colorYellowLight
      })
      this.cEnemy.push(c)
    },
    lifeDetect() {
      if (this.life.heart <= 0) {
        this.isOver = true
        clearInterval(this.lifeChecker)
      }
    },
    drawStartPage() {
      this.context.save()
      this.context.translate(this.wCenter, this.hCenter)
      // 同心圓
      let circleColors = ['white', 'rgba(255, 255, 255, .2)']
      for (let i = 0; i < 2; i++) {
        this.drawCircle(0, 0, 150 + i * 100, 1, circleColors[i])
      }

      // 中央文字
      this.context.font = '16px sans-serif'
      this.context.textAlign = 'center'
      this.context.fillText('Radio Defense', 0, 0)

      // R
      this.context.font = '78px sans-serif'
      this.context.fillText('R', 24, -24)
      this.context.restore()
      this.drawBattery(this.wCenter - 24, this.hCenter - 19 - 24)

      // 右上圓
      this.context.beginPath()
      this.context.save()
      this.context.translate(300, -200)
      this.context.arc(this.wCenter, this.hCenter, 35, 0, Math.PI / 180 * 360)
      this.context.fillStyle = this.colorYellow
      this.context.fill()
      this.context.restore()
      this.context.closePath()

      // 右下三角
      this.drawPolygon(
        this.wCenter + 243,
        this.hCenter + 200,
        3,
        60,
        0,
        '',
        this.colorBlue,
        47
      )
      // 左上多邊
      this.context.beginPath()
      this.context.save()
      this.context.translate(-360, -140)
      this.context.moveTo(this.wCenter, this.hCenter)
      this.context.lineTo(this.wCenter + 28, this.hCenter - 10)
      this.context.lineTo(this.wCenter + 58, this.hCenter + 3)
      this.context.lineTo(this.wCenter + 62, this.hCenter + 32)
      this.context.lineTo(this.wCenter + 43, this.hCenter + 56)
      this.context.lineTo(this.wCenter + 12, this.hCenter + 40)
      this.context.fillStyle = this.colorRed
      this.context.fill()
      this.context.restore()
      this.context.closePath()
      // 左下文字
      this.context.beginPath()
      this.context.save()
      this.context.translate(-450, 200)
      this.context.fillStyle = 'white'
      this.context.font = '16px "微軟正黑體", "Microsoft JhengHei", sans-serif'
      this.context.textAlign = 'left'
      this.context.fillText(
        '你身負著運送能量電池的任務',
        this.wCenter,
        this.hCenter
      )
      this.context.fillText(
        '卻遭到幾何星人的埋伏',
        this.wCenter,
        this.hCenter + 24
      )
      this.context.fillText(
        '請協助從他們的手中奪回能量電池',
        this.wCenter,
        this.hCenter + 48
      )
      this.context.fill()
      this.context.restore()
      this.context.closePath()
    },
    startGame() {
      console.log('start!')
      this.isStart = !this.isStart
      this.context.clearRect(0, 0, this.canvas.width, this.canvas.height)
      this.drawGrid()
      this.drawGame()
    },
    drawGame() {
      this.context.clearRect(0, 0, this.canvas.width, this.canvas.height)
      // R
      this.context.font = '78px sans-serif'
      this.context.fillText('R', 24, 78)
      // Logo text
      this.context.font = '16px sans-serif'
      // this.context.textAlign = 'center'
      this.context.fillText('Radio Defense', 24, 100)
      this.drawWeapon(this.wCenter, this.hCenter)
      requestAnimationFrame(this.drawGame)
    },
    drawBattery(centerX, centerY) {
      // this.context.save()
      this.context.translate(centerX, centerY)
      // 電池 - 頭
      this.context.fillRect(-5, -15 - 4, 10, 4)
      // 電池 - 身
      this.context.fillStyle = this.color.yellow
      this.context.fillRect(-10, -15, 20, 30)
      // 電池 - 尾
      this.context.fillRect(-10, -15 + 30 + 1, 20, 4)
      // 電池 - 閃電
      this.context.beginPath()
      this.context.moveTo(-4, 0)
      this.context.lineTo(2, 0)
      this.context.lineTo(-5, 10)
      this.context.lineTo(6, -2)
      this.context.lineTo(0, -2)
      this.context.lineTo(4, -10)
      this.context.fillStyle = 'white'
      this.context.fill()
      this.context.translate(-centerX, -centerY)
    },
    drawCircle(
      centerX,
      centerY,
      radius,
      strokeWidth,
      strokeColor,
      shadowBlur,
      shadowColor
    ) {
      this.context.save()
      this.context.beginPath()
      this.context.arc(
        centerX,
        centerY,
        radius,
        Math.PI / 180 * 0,
        Math.PI / 180 * 360,
        true
      )
      this.context.closePath()
      this.context.shadowBlur = shadowBlur
      this.context.shadowColor = shadowColor
      this.context.lineWidth = strokeWidth
      this.context.strokeStyle = strokeColor
      this.context.stroke()
      this.context.restore()
    },
    drawPolygon(
      centerX,
      centerY,
      sideCount,
      size,
      strokeWidth,
      strokeColor,
      fillColor,
      rotationDegrees
    ) {
      let radians = rotationDegrees * Math.PI / 180
      this.context.translate(centerX, centerY)
      this.context.rotate(radians)
      this.context.beginPath()
      this.context.moveTo(size * Math.cos(0), size * Math.sin(0))
      for (let i = 1; i <= sideCount; i += 1) {
        this.context.lineTo(
          size * Math.cos(i * 2 * Math.PI / sideCount),
          size * Math.sin(i * 2 * Math.PI / sideCount)
        )
      }
      this.context.closePath()
      this.context.fillStyle = fillColor
      this.context.strokeStyle = strokeColor
      this.context.lineWidth = strokeWidth
      this.context.stroke()
      this.context.fill()
      this.context.rotate(-radians)
      this.context.translate(-centerX, -centerY)
    },
    mousemoveHandler(event) {
      this.mouse.x = event.offsetX
      this.mouse.y = event.offsetY
      // this.angle = this.mouse.x / 50
      this.angle = Math.atan2(
        this.mouse.y - this.hCenter,
        this.mouse.x - this.wCenter
      )
    },
    mouseupHandler() {},
    mousedownHandler() {}
  }
}
</script>

<template src="./template.html">
</template>

<style lang="scss" src="./style.scss" scoped>
</style>



