export class Life {
  constructor(args) {
    let def = {
      x: 0,
      y: 0,
      w: 300,
      h: 200,
      value: 50,
      heart: 3
    }
    Object.assign(def, args)
    Object.assign(this, def)
  }
  update() {
    if (this.heart > 0) {
      this.value -= 30
      if (this.value < 0) {
        this.heart--
        this.value = this.heart > 0 ? 100 : 0
      }
    } else {
      this.value = 0
    }
  }
  draw(ctx) {
    ctx.save()
    ctx.translate(this.x, this.y)
    ctx.beginPath()
    ctx.strokeStyle = 'white'
    ctx.lineWidth = 2
    ctx.moveTo(0, 0)
    ctx.lineTo(-this.w, 0)
    ctx.lineTo(-this.w, this.h)
    ctx.lineTo(0, this.h)
    ctx.lineTo(0, 0)
    ctx.stroke()

    ctx.translate(-this.w, 0)
    ctx.beginPath()
    ctx.fillStyle = 'rgb(245, 175, 95)'
    let ln = (this.value / 100) * this.w
    // ln = ln < 0 ? this.w : ln
    ctx.fillRect(0, 0, ln, this.h)
    ctx.closePath()

    ctx.translate(this.w - 30, this.h * 3)
    for (let i = 0; i < this.heart; i++) {
      ctx.beginPath()
      ctx.arc(0, 0, 30, 0, Math.PI * 2)
      ctx.fillStyle = 'rgb(231, 70, 93)'
      ctx.fill()
      ctx.translate(-80, 0)
    }
    ctx.restore()
  }
}



