// let degToPi = (Math.PI * 2) / 360

export class CBullet {
  constructor(args) {
    let def = {
      x: 0,
      y: 0,
      er: 0,
      sapn: 0,
      deg: 0,
      v: {
        x: 0,
        y: 0
      }
    }
    Object.assign(def, args)
    Object.assign(this, def)
  }
  update() {
    this.x -= this.v.x
    this.y -= this.v.y
  }
  draw(ctx) {
    let bulletRadius = 10
    ctx.save()
    ctx.translate(this.x, this.y)
    ctx.rotate(this.deg)

    ctx.fillStyle = 'white'
    // ctx.translate(this.span - this.er - 30, 0)
    ctx.beginPath()
    ctx.arc(0, 0, bulletRadius, Math.PI / 2, -Math.PI / 2)
    ctx.fill()

    // ctx.beginPath()
    ctx.moveTo(30, 0)
    ctx.lineTo(0, 10)
    ctx.lineTo(0, -10)
    ctx.fill()
    ctx.restore()
  }
}



