export class Bullet {
  constructor(args) {
    let def = {
      x: 0,
      y: 0,
      v: {
        x: 0,
        y: 0
      }
    }
    Object.assign(def, args)
    Object.assign(this, def)
  }
  update() {
    this.x += this.v.x
    this.y += this.v.y
  }
  draw(ctx) {
    ctx.save()
    ctx.translate(this.x, this.y)
    ctx.beginPath()

    ctx.fillStyle = 'white'
    // ctx.fillRect(0, 0, 10, 10)
    ctx.arc(0, 0, 5, (Math.PI / 180) * 0, (Math.PI / 180) * 360)
    ctx.fill()
    ctx.restore()
  }
}



