import { CBullet } from '../CBullet'

let degToPi = (Math.PI * 2) / 360

export class CircleEnemy {
  constructor(args) {
    let def = {
      x: 0,
      y: 0,
      r: 10,
      span: 0,
      deg: 0 * degToPi,
      color: 'white',
      color2: 'rgba(255, 255, 255, .2)',
      bullets: [],
      clockwise: true
    }
    Object.assign(def, args)
    Object.assign(this, def)
  }
  update() {
    if (this.clockwise) {
      this.deg += 0.01
    } else {
      this.deg -= 0.01
    }
    if (this.deg > 360 * degToPi) {
      this.deg %= 360 * degToPi
    }
  }
  draw(ctx) {
    ctx.save()

    /* 以下三行有順序性 */
    ctx.translate(this.x, this.y) // 先定位到中間
    ctx.rotate(this.deg) // 旋轉至對應角度
    ctx.translate(this.span, 0) // 拉到 span 的距離繪圖
    // 虛圓
    ctx.beginPath()
    ctx.arc(10, 0, this.r, 0, Math.PI * 2)
    ctx.fillStyle = 'rgba(255, 255, 255, .2'
    ctx.fill()
    // 實圓
    ctx.beginPath()
    ctx.arc(0, 0, this.r, 0, Math.PI * 2)
    ctx.fillStyle = this.color
    ctx.fill()
    for (let i = 0; i < 3; i++) {
      ctx.beginPath()
      ctx.fillStyle = 'white'
      ctx.arc(20, 0, 4, 0, Math.PI * 2)
      ctx.fill()

      ctx.beginPath()
      ctx.moveTo(16, 0)
      ctx.lineTo(0, 4)
      ctx.lineTo(0, -4)
      ctx.fill()
      ctx.rotate(120 * degToPi)
    }
    ctx.translate(-this.x, -this.y)
    ctx.restore()
  }
  addBullet() {
    // console.log(this.x, this.y)

    let cb = new CBullet({
      x: this.x + this.span * Math.cos(this.deg),
      y: this.y + this.span * Math.sin(this.deg),
      er: this.r,
      span: this.span,
      deg: this.deg,
      v: {
        x: Math.cos(this.deg) * 2,
        y: Math.sin(this.deg) * 2
      }
    })
    this.bullets.push(cb)
  }
}



