<template>
  <div id="app">
    <StartPage :color="color"></StartPage>
  </div>
</template>

<script>
import StartPage from '@/pages/StartPage'

export default {
  name: 'App',
  components: {
    StartPage
  },
  data() {
    return {
      color: {
        yellow: '#F5AF5F',
        blue: '#3676BB',
        red: '#E7465D'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
#app {
  position: relative;
}
</style>



