let degToPi = (Math.PI * 2) / 360

export class Weapon {
  constructor(args) {
    let def = {
      x: 0,
      y: 0,
      r1: 50,
      r2: 100,
      r3: 120,
      r4: 400,
      deg: 0 * degToPi
    }
    Object.assign(def, args)
    Object.assign(this, def)
  }
  draw(ctx) {
    ctx.save()
    ctx.translate(this.x, this.y)

    // 內光暈圓
    ctx.beginPath()
    ctx.arc(0, 0, this.r1, 0, Math.PI * 2)
    ctx.strokeStyle = 'white'
    ctx.lineWidth = 12
    ctx.shadowBlur = 20 // canvas 版陰影, 產生光暈效果
    ctx.shadowColor = 'white'
    ctx.stroke()
    // benz
    for (let i = 0; i < 3; i++) {
      ctx.lineWidth = 5
      ctx.beginPath()
      ctx.moveTo(0, 0)
      ctx.lineTo(0, -this.r1)
      ctx.stroke()
      ctx.rotate((Math.PI / 180) * 120) // 每畫完一條線, 旋轉 120 度
    }
    ctx.shadowBlur = 0
    ctx.lineWidth = 1

    ctx.rotate(this.deg)
    // 外虛線圓
    ctx.beginPath()
    ctx.arc(0, 0, this.r2, (Math.PI / 180) * 15, (Math.PI / 180) * -15)
    ctx.strokeStyle = 'white'
    ctx.lineWidth = 3
    ctx.setLineDash([5])
    ctx.stroke()
    ctx.setLineDash([0])

    // 砲台
    ctx.fillStyle = 'white'
    ctx.lineWidth = 1
    ctx.beginPath()
    ctx.moveTo(this.r2, 0)
    ctx.lineTo(this.r2, 5)
    ctx.lineTo(this.r2 - 10, 10)
    ctx.lineTo(this.r2 - 30, 10)
    ctx.lineTo(this.r2 - 30, -10)
    ctx.lineTo(this.r2 - 10, -10)
    ctx.lineTo(this.r2, -5)
    ctx.fill()

    // 盾牌
    ctx.beginPath()
    ctx.arc(0, 0, this.r3, 135 * degToPi, 225 * degToPi)
    ctx.lineWidth = 5
    ctx.strokeStyle = 'white'
    ctx.stroke()

    // 敵軍環繞的軌跡示意
    ctx.beginPath()
    ctx.arc(0, 0, this.r4, 0, Math.PI * 2)
    ctx.strokeStyle = 'rgba(255, 255, 255, .4)'
    ctx.stroke()
    ctx.restore()
  }
}



