import moment from 'moment'

export class Timer {
  constructor(args) {
    let def = {
      x: 0,
      y: 0,
      text: '01:00',
      deadline: moment().add(1, 'm'),
      timesup: false
    }
    Object.assign(def, args)
    Object.assign(this, def)
  }
  draw(ctx) {
    ctx.save()
    ctx.font = '40px "Roboto", sans-serif'
    ctx.textAlign = 'center'
    ctx.fillStyle = 'white'
    ctx.fillText(this.text, this.x, this.y, 120)
    ctx.restore()
  }
  update() {
    let timestamp = this.deadline.valueOf() - moment().valueOf()
    this.text = moment(timestamp).format('mm:ss')
    let sec = moment(timestamp).format('s')

    if (sec === '0') {
      this.timesup = true
    }
  }
}



